import React from 'react'
import {Line} from 'react-chartjs-2';

class Graph extends React.Component {
    constructor(props) {
        super()
        let data = props.data
        let labelsCases = [],
        dataPointsCases = [],
        labelsDeaths = [],
        dataPointsDeaths = []
        for (let i = 0; i < data.length; i++) {
            labelsCases.push(`${data[data.length - 1 - i].date.month}/${data[data.length - 1 - i].date.day}`)
            dataPointsCases.push(data[data.length - 1 - i].total)
            labelsDeaths.push(`${data[data.length - 1 - i].date.month}/${data[data.length - 1 - i].date.day}`)
            dataPointsDeaths.push(data[data.length - 1 - i].death)
        }
        this.state = {
            graphData: {
                labels: labelsCases,
                datasets: [
                    {
                        label: 'Cases over time',
                        fill: false,
                        lineTension: 0.1,
                        backgroundColor: 'rgba(0, 0, 0, 1)',
                        borderColor: 'rgba(75,192,192,1)',
                        borderCapStyle: 'butt',
                        borderDash: [],
                        borderDashOffset: 0.0,
                        borderJoinStyle: 'miter',
                        pointBorderColor: 'rgba(75,192,192,1)',
                        pointBackgroundColor: '#fff',
                        pointBorderWidth: 1,
                        pointHoverRadius: 5,
                        pointHoverBackgroundColor: 'rgba(75,192,192,1)',
                        pointHoverBorderColor: 'rgba(220,220,220,1)',
                        pointHoverBorderWidth: 2,
                        pointRadius: 1,
                        pointHitRadius: 10,
                        data: dataPointsCases
                    },
                    {
                        label: 'Deaths over time',
                        fill: false,
                        lineTension: 0.1,
                        backgroundColor: 'red',
                        borderColor: 'red',
                        borderCapStyle: 'butt',
                        borderDash: [],
                        borderDashOffset: 0.0,
                        borderJoinStyle: 'miter',
                        pointBorderColor: 'red',
                        pointBackgroundColor: '#fff',
                        pointBorderWidth: 1,
                        pointHoverRadius: 5,
                        pointHoverBackgroundColor: 'red',
                        pointHoverBorderColor: 'dark-red',
                        pointHoverBorderWidth: 2,
                        pointRadius: 1,
                        pointHitRadius: 10,
                        data: dataPointsDeaths
                    }
                ],
                options: {
                    scales: {
                        yAxes: [{
                            display: true,
                            gridLines: {
                                display: false ,
                                color: "#ff0000"
                            },
                            ticks: {
                                beginAtZero: true
                            }
                        }],
                        xAxes: [{
                            display: true,
                            gridLines: {
                                color: "#FFFFFF"
                            }
                        }]
                    }
                }
            }
        }
    }

    render() {
        return (
            <div class="Graph">
                <Line data={this.state.graphData} />
            </div>
        )
    }
}

export default Graph